@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@-webkit-keyframes heading-animation {
  0% {
    transform: translateY(3px);
  }
  50% {
    transform: translateY(-3px);
    text-shadow: -10px 8px #ce015a;
  }
  100% {
    transform: translateY(3px);
  }
}

@keyframes heading-animation {
  0% {
    transform: translateY(3px);
  }
  50% {
    transform: translateY(-3px);
    text-shadow: -10px 8px #ce015a;
  }
  100% {
    transform: translateY(3px);
  }
}
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  font-family: "Orbitron", sans-serif;
  background-color: #e63946;
}

.calculator-heading {
  color: #f1faee;
  font-size: 50px;
  margin-bottom: 50px;
  text-shadow: -5px 3px #ce015a;
  -webkit-animation-name: heading-animation;
          animation-name: heading-animation;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
.created-by-message {
  margin-top: 20px;
  color: white;
}
@media only screen and (max-width: 768px) {
  .calculator-heading {
    margin-top: -30px;
    font-size: 35px;
  }
}

@-webkit-keyframes calculator-animation {
  0% {
    transform: translateY(3px);
  }
  50% {
    transform: translateY(-3px);
    box-shadow: -15px 15px 3px 1px #ce015a;
  }
  100% {
    transform: translateY(3px);
  }
}
@keyframes calculator-animation {
  0% {
    transform: translateY(3px);
  }
  50% {
    transform: translateY(-3px);
    box-shadow: -15px 15px 3px 1px #ce015a;
  }
  100% {
    transform: translateY(3px);
  }
}
.calculator-container {
  width: 400px;
  height: 550px;
  background-color: #f1faee;
  border-radius: 5px;
  transition-duration: 0.1s;
  -webkit-animation-name: calculator-animation;
          animation-name: calculator-animation;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  box-shadow: -5px 5px 1px 1px #ce015a;
}

.calculator-display {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  flex-direction: column;
  background-color: rgb(0, 0, 0);
  height: 20%;
  margin: 5px;
  box-shadow: inset 0px -1px 1px 1px #fcbf49;
}
.calculator-display-message {
  color: #ffb700;
  margin-right: 15px;
  font-size: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10px;
}
.calculator-full-message {
  font-size: 15px;
  color: red;
  margin-right: 15px;
}
.calculator-buttons-container {
  height: 400px;
  background-color: rgba(82, 80, 80, 0.062);
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  margin: 10px;
  border-radius: 5px;
}
.buttons-row {
  display: grid;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
          column-gap: 10px;
  height: 70px;
}
#first-row {
  grid-template-columns: 2.2fr 1fr 1fr;
}
#second-row {
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

#third-row {
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
#fourth-row {
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
#fifth-row {
  grid-template-columns: 1fr 1fr 2.2fr;
}
#clear {
  background-color: #fa543e;
}
#clear:active {
  background-color: #c71e08;
}

.calculator-button {
  background-color: #a8dadc;
  box-shadow: -1px 2px 1px 1px black;
  display: grid;
  align-content: center;
  justify-content: center;
  border-radius: 5px;
  border: none;
  font-family: "Orbitron", sans-serif;
  font-size: 25px;
  cursor: pointer;
  transition-duration: 0.1s;
}
.calculator-button:active {
  background-color: #52b7bb;
  transform: translate(-2px, 2px);
  box-shadow: 0px 0px 1px 0px black;
  color: white;
}
.operators {
  background-color: #fcbf49;
}
.operators:active {
  background-color: #03045e;
}
@media only screen and (max-width: 768px) {
  .calculator-container {
    width: 350px;
  }
  .calculator-display-message {
    font-size: 20px;
  }
}

